.button {
    width: 132px;
    height: 30px;
    color:white
}

.disabled {

    background: rgba(0, 77, 153, 0.33);
    color: grey
}

.secondary {
    width: 176px;
    height: 30px;
    border: 1px solid rgba(0, 102, 204, 1);
    color: rgba(0, 102, 204, 1);

}

.default {
    background: rgba(0, 102, 204, 1);
}

.red {
    background: rgba(204, 20, 57, 1);

}

.default:hover {
    width: 167px;
    height: 30px;
}

.red:hover {
    width: 167px;
    height: 30px;
}

.secondary:hover {
    width: 167px;
    height: 30px;
}


.default:active {
    background: rgba(0, 89, 178, 1);

}

.secondary:active {
    border: 1px solid rgba(0, 102, 204, 1);
    background: rgba(201, 229, 255, 1);
    color: rgba(0, 102, 204, 1);

}

.red:active {
    background: rgba(178, 18, 50, 1);

}

.button:disabled {
}
