.header {
    display: flex;
    max-width: 1280px;
    width: 100%; /* Растягиваем на всю ширину экрана */
    flex-direction: row;
    justify-content: space-between;
    padding: 20px 50px ;
    margin: 0 auto;

}

.header h1 {
    font-family: Montserrat, sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 29px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;

}