    .sidebar {
    display: none;
    }

    .sidebar.open {
        display: block;
        position: fixed;
        background-color: white;
        width: 317px;
        height:100vh;
        border: 1px black solid;


    }

    .background {
        position: fixed; /* Чтобы растянуть элемент на весь экран */
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.5);
        cursor: pointer;
    }

    .nav {
        display: flex;
        flex-direction: column;
        align-items: start;
        font-family: 'Montserrat', sans-serif;
        font-weight: 600;
        font-size: 22px;
        color: black;
        margin-left: 35px;
        margin-top: 45px;

        text-decoration: none}

    .nav a {
    }

    .nav a:hover {
        color: #58ACFF;
    }

    .nav a.active {
        text-decoration: underline;
        color: #3D6B99;
        margin-left: 35px;
        margin-top: 45px;



    }
    .close {
        position: absolute;
        top: 10px;
        right: 10px;
        display: flex;
        background-color: white;
        border: none;


    }

    .close img {
        width: 100%;
        height: auto;
    }
